<script>
import axios from "axios";

export default {
  components: {

  },
  data() {
    return {
        idArchivo: null,
        archivo: {
        nombre_archivo: "",
        url:""
      }

    };
  },
  async mounted() {
    this.idArchivo = this.$route.params.idArchivo
 
  },
  mounted(){
    this.idArchivo = this.$route.params.idArchivo
    this.verArchivos()
  },
  methods: {

    verArchivos() {
      //  alert("aaaa")
        axios.get("http://localhost:3000/verDetalleArchivo/"+this.idArchivo )
          .then((res) => {
            //codigo
           
            this.archivo= res.data
            console.log(this.archivo);
            // this.archivo.url = res.data.url
          })
          .catch((err) => {
            //TODO mostrar ventana emergente sweetalert2 con el error
            alert("error del servidor")
          })
      
  
    },
    EditarArchivos() {
      // alert("aaaa")
      axios.put("http://localhost:3000/editarArchivo/"+this.idArchivo,this.archivo)
        .then((res) => {
          //codigo
          console.log(res);
          this.$router.push("/archivo");
          //ya se edit
        })
        .catch((err) => {
          //TODO mostrar ventana emergente sweetalert2 con el error
